@use 'base/variables' as v;
@use 'base/mixins' as m;

.iconos {
    margin-top: 5rem;
    .listado-iconos {
        @include m.tablet { 
            @include m.grid(3, 2rem);
        }

        li {
            i {
                font-size: 12rem;
                color: v.$primario;
                margin: 0 auto;
                display: block;
            }
            p {
                font-family: v.$fuenteCursiva;
                font-size: 3.2rem;
                color: v.$primario;
            }
        }
    }
}
