html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #D9CAB8;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #000000;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1, h2, .heading-blanco, h3 {
  font-family: "Raleway", sans-serif;
  margin: 0 0 5rem 0;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2, .heading-blanco {
  font-size: 3.6rem;
  color: #8B1A1A;
}
h2 span, .heading-blanco span {
  color: #D4AF37;
  font-size: 1.8rem;
  display: block;
}
h2::after, .heading-blanco::after {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
  background-image: url(../img/NuevoDeLaFinca.webp);
  background-size: 10rem;
}

.heading-blanco {
  color: #F7F3EE;
}
.heading-blanco span {
  color: #F7F3EE;
}
.heading-blanco::after {
  display: none;
}

h3 {
  font-size: 3.2rem;
}

.header {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .header {
    min-height: 70rem;
  }
}
.header::before {
  content: "";
  background: rgba(0, 0, 0, 0.9);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.9;
}
.header .contenido-header {
  position: relative;
}
.header .contenido-header h1 {
  text-align: center;
  color: #F7F3EE;
  text-transform: uppercase;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .header .contenido-header h1 {
    margin-top: 10rem;
  }
}

.header-inicio {
  background-image: url("/build/img/header_de_la_finca.jpg");
}

.header-nosotros {
  background-image: url("/build/img/header_nosotros.jpg");
}

.header-proceso {
  background-image: url("/build/img/header_proceso.jpg");
}

.header-menu {
  background-image: url("/build/img/header_menu.jpg");
}

.header-galeria {
  background-image: url("/build/img/header_galeria.jpg");
}

.header-contacto {
  background-image: url("/build/img/header_contacto.jpg");
}

.nav-principal {
  display: flex;
  flex-direction: column;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .nav-principal {
    flex-direction: row;
    gap: 1rem;
    margin-top: 0;
  }
}
.nav-principal a {
  text-align: center;
  color: #F7F3EE;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .nav-principal a {
    margin-bottom: 0;
  }
}
.nav-principal a:hover {
  color: #7C1F29;
}
.nav-principal a.activo {
  color: #7C1F29;
}

.barra {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.barra .logo {
  margin-right: auto;
}
.barra .logo .logo-img {
  width: auto;
  max-width: 200px;
  padding-right: 3rem;
}
@media (min-width: 768px) {
  .barra .logo .logo-img {
    max-width: 150px;
    padding-right: 3rem;
  }
}
.barra .menu-hamburguesa {
  display: block;
  cursor: pointer;
  width: 3rem;
  height: 2.5rem;
  position: relative;
  z-index: 10;
}
.barra .menu-hamburguesa span {
  background-color: #F7F3EE;
  display: block;
  height: 0.3rem;
  margin: 0.5rem 0;
  transition: all 0.3s ease;
}
@media (min-width: 768px) {
  .barra .menu-hamburguesa {
    display: none;
  }
}
.barra .nav-principal {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 9;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s ease, visibility 0.5s ease;
}
.barra .nav-principal a {
  padding: 1rem;
  text-align: center;
  display: block;
  font-size: 2rem;
  color: #F7F3EE;
  background-color: #8B1A1A;
  margin: 5px 0;
  transition: background-color 0.3s ease;
  border-radius: 5px;
}
.barra .nav-principal a:hover {
  background-color: #D4AF37;
}
.barra .nav-principal.activo {
  display: block;
  opacity: 1;
  visibility: visible;
}
@media (min-width: 768px) {
  .barra .nav-principal {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: static;
    background-color: transparent;
    opacity: 1;
    visibility: visible;
  }
  .barra .nav-principal a {
    padding: 1rem 2rem;
    font-size: 1.8rem;
    background-color: transparent;
    color: #F7F3EE;
    margin: 0;
  }
  .barra .nav-principal a:hover {
    background-color: transparent;
    color: #D4AF37;
  }
}
@media (max-width: 840px) and (min-width: 740px) {
  .barra .nav-principal a {
    padding: 1rem;
    font-size: 1.6rem;
    margin: 0.5rem;
  }
}

.menu-hamburguesa.active span:nth-child(1) {
  transform: translateY(8px) rotate(45deg);
}

.menu-hamburguesa.active span:nth-child(2) {
  opacity: 0;
}

.menu-hamburguesa.active span:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg);
}

.footer {
  background-color: #8B1A1A;
}
.footer .contenido-footer {
  padding: 5rem;
}
@media (min-width: 768px) {
  .footer .contenido-footer {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.footer .copyright {
  text-align: center;
  margin: 0;
  padding: 2rem;
  color: #F7F3EE;
}

.footer-box {
  font-family: "Bona Nova", serif;
  text-align: center;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .footer-box {
    margin-bottom: 0;
  }
}
.footer-box h3 {
  font-family: "Bona Nova", serif;
  margin: 0 0 1rem 0;
  font-size: 2.4rem;
  color: #D4AF37;
}
.footer-box p, .footer-box a {
  font-size: 2rem;
  margin: 0;
}

.contenido-principal {
  background-color: #F7F3EE;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .contenido-principal {
    padding: 10rem;
    margin-top: -10rem;
    position: relative;
  }
}

.iconos {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .iconos .listado-iconos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.iconos .listado-iconos li i {
  font-size: 12rem;
  color: #8B1A1A;
  margin: 0 auto;
  display: block;
}
.iconos .listado-iconos li p {
  font-family: "Bona Nova", serif;
  font-size: 3.2rem;
  color: #8B1A1A;
}

.galeria {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 1024px) {
  .galeria {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.galeria .imagen {
  overflow: hidden;
}
.galeria .imagen img {
  height: 30rem;
  width: 30rem;
  object-fit: cover;
  transition: transform 0.3s ease, filter 0.3s ease;
  filter: grayscale(10%);
}
.galeria .imagen:hover img {
  transform: scale(1.1);
  filter: grayscale(0%);
}
@media (min-width: 1024px) {
  .galeria .imagen:nth-child(1) {
    grid-row: 1/3;
  }
  .galeria .imagen:nth-child(1) img {
    height: 62rem;
    object-position: center;
  }
  .galeria .imagen:nth-child(2) {
    grid-column: 2/4;
  }
  .galeria .imagen:nth-child(2) img {
    width: 62rem;
  }
  .galeria .imagen:nth-child(4) {
    grid-row: 2/4;
  }
  .galeria .imagen:nth-child(4) img {
    height: 62rem;
  }
  .galeria .imagen:nth-child(5) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(5) img {
    width: 62rem;
  }
  .galeria .imagen:nth-child(6) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(6) img {
    width: 62rem;
  }
  .galeria .imagen:nth-child(7) {
    grid-row: 3/5;
  }
  .galeria .imagen:nth-child(7) img {
    height: 62rem;
  }
  .galeria .imagen:nth-child(8) {
    grid-column: 2/5;
  }
  .galeria .imagen:nth-child(8) img {
    width: 94rem;
  }
  .galeria .imagen:nth-child(9) {
    grid-column: 1/3;
  }
  .galeria .imagen:nth-child(9) img {
    width: 62rem;
  }
  .galeria .imagen:nth-child(10) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(10) img {
    width: 62rem;
  }
}

.nuestro-menu {
  background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg), linear-gradient(to top, #8B1A1A, #8B1A1A);
  background-repeat: no-repeat;
  background-position: center 5rem, center 92%, 100%;
  background-size: 200%;
  padding: 15rem 2rem 20rem 2rem;
}
@media (min-width: 768px) {
  .nuestro-menu {
    padding: 15rem 0 20rem 0;
    background-size: 100%;
  }
}
@media (min-width: 1024px) {
  .nuestro-menu {
    background-size: 50%, 50%, 100%;
  }
}

.grid-menu {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-auto-rows: minmax(150px, auto);
  gap: 2rem;
}
@media (min-width: 768px) {
  .grid-menu {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .grid-menu {
    grid-template-columns: repeat(2, 1fr);
  }
}

.menu h3 {
  color: #F7F3EE;
}
.menu li {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  margin-bottom: 3rem;
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  min-height: 120px;
}
.menu li p {
  color: #F7F3EE;
  font-size: 2rem;
  line-height: 1.5;
  margin: 0 0 0.5rem 0;
}
.menu li p span {
  display: block;
  color: #D4AF37;
  font-size: 1.8rem;
}
.menu li .nombre-precio {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
}
.menu li .nombre-precio .nombre {
  color: #F7F3EE;
  font-size: 2.2rem;
  font-weight: 700;
}
.menu li .nombre-precio .precio {
  color: #D4AF37;
  font-size: 2.2rem;
  font-weight: 700;
  text-align: right;
  min-width: 100px;
}
.menu li .descripcion {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 8;
  -webkit-box-orient: vertical;
  line-clamp: 3;
}

.menu-completo-enlace {
  margin-top: 3rem;
  text-align: center;
}
.menu-completo-enlace a.boton {
  display: inline-block;
  padding: 1rem 2rem;
  background-color: #D4AF37;
  color: #000000;
  text-transform: uppercase;
  font-size: 1.8rem;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}
.menu-completo-enlace a.boton:hover {
  background-color: #8B1A1A;
  color: #F7F3EE;
}
@media (min-width: 768px) {
  .menu-completo-enlace a.boton {
    font-size: 2rem;
    padding: 1.5rem 3rem;
  }
}
@media (min-width: 1024px) {
  .menu-completo-enlace a.boton {
    font-size: 2.2rem;
    padding: 1.8rem 4rem;
  }
}

.descripcion {
  font-size: 1.8rem;
  color: #F7F3EE;
  line-height: 1.4;
}
.descripcion br + span {
  font-size: 1.6rem;
  color: #D4AF37;
  font-style: italic;
}
.descripcion span.language-en {
  color: #a7e089;
}
.descripcion span.language-fr {
  color: #F4D35E;
}

.testimoniales-container {
  width: 100%;
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 1rem;
}
.testimoniales-container h2 {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
  color: #8B1A1A;
  width: 100%;
}
.testimoniales-container .img-tripadvisor {
  width: 250px;
  height: auto;
  margin-bottom: 20px;
  border-radius: 8px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}
.testimoniales-container .img-tripadvisor:hover {
  transform: scale(1.08);
  box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.2);
}
@media (min-width: 768px) {
  .testimoniales-container .img-tripadvisor {
    width: 300px;
  }
}
.testimoniales-container > div {
  flex: 1 1 100%;
  max-width: 100%;
  border-radius: 0.5rem;
  transition: 0.5s ease-in-out;
  cursor: pointer;
  box-shadow: 1px 5px 15px #1e0e3e;
  position: relative;
  overflow: hidden;
  height: auto;
}
.testimoniales-container > div:nth-of-type(1) {
  background: url("../img/galeria/comida2.jpg") no-repeat 50%/cover;
  filter: brightness(0.6);
}
.testimoniales-container > div:nth-of-type(2) {
  background: url("../img/galeria/comida3.jpg") no-repeat 50%/cover;
  filter: brightness(0.6);
}
.testimoniales-container > div:nth-of-type(3) {
  background: url("../img/galeria/comida4.jpg") no-repeat 50%/cover;
  filter: brightness(0.6);
}
.testimoniales-container > div:nth-of-type(4) {
  background: url("../img/galeria/comida6.jpg") no-repeat 50%/cover;
  filter: brightness(0.6);
}
.testimoniales-container > div:nth-of-type(5) {
  background: url("../img/galeria/costilla.jpg") no-repeat 50%/cover;
  filter: brightness(0.6);
}
.testimoniales-container > div .testimoniales-content {
  font-size: 1.5rem;
  color: #ffffff;
  display: flex;
  align-items: center;
  padding: 15px;
  opacity: 0;
  flex-direction: column;
  height: 100%;
  justify-content: flex-end;
  background: #02022e;
  background: linear-gradient(0deg, rgba(2, 2, 46, 0.8) 0%, rgba(0, 0, 0, 0) 100%);
  transform: translateY(100%);
  transition: opacity 0.5s ease-in-out, transform 0.5s 0.2s;
